<script>
import { ref } from "vue";
//const tooltip = ref(null);
const dataMap = require("@/assets/img/ss.svg");

export default {
  name: "ProjectDistributionByState",
  props: ["mapData"],
  data() {
    return {
      mapElement: null,
      //tooltipElement: null,
      toolTip: {
        state: "",
        projects: 0,
        funding: 0,
        organisations: "",
        percentage: "",
      },
      isFalse: ref(null),
    };
  },
  computed: {
    dataMap() {
      return dataMap;
    },
    tooltipElement() {
      return document.querySelector("#tooTip");
    },
  },
  methods: {
    // ...mapActions({
    //   fetchData : 'reports/getFundingBySectorReport'
    // })

    enter(e) {
      if (e.target.nodeName == "path") {
        let details = e.target.attributes;
        this.toolTip.state = details.name.value;
        this.toolTip.projects = this.mapData.find(
          (element) => element.wikidataid == details.wikidataid.value
        )?.projects;
        this.toolTip.funding = this.mapData.find(
          (element) => element.wikidataid == details.wikidataid.value
        )?.funding;
        this.toolTip.organisations = this.mapData.find(
          (element) => element.wikidataid == details.wikidataid.value
        )?.organisations;
        this.$refs.tooltip.style.transform = `translate(${e.offsetX}px, ${e.offsetY}px)`;

        e.target.style.opacity = 0.6;
      }
    },
    leave: (e) => {
      if (e.target.nodeName == "path") {
        e.target.style.opacity = 1;
        //console.log(e.target)
      }
    },
  },
  mounted() {
    //this.simplemaps_usmap_mapdata.main_settings.auto_load = 'no';
    //    this.loadMap();
    //    console.log(this.$refs.tooltip)
    //this.tooltipElement = this.$refs.tooltip
  },
};
</script>

